import {TokenTable} from './tokenTable';
import {TransactionReceipt} from '../../eos/transactionReceipt';
import {Constants} from '../../eos/constants';
import {Network} from '../../eos/network';
import {Contract} from '@/modules/eos/contract';
import {EOS} from '@/modules/eos/eos';
import {Token} from '@/modules/eos/token';
import {PlayerTable} from '@/modules/contract/house/playerTable';

export class HouseContract extends Contract {
    public static readonly CONTRACT: string = Constants.NETWORK === Network.LOCALHOST ? 'house' : 'godapphouse1';

    public constructor(eos: EOS) {
        super(HouseContract.CONTRACT, eos);
    }

    public makeTokenTable(name: string): TokenTable {
        return new TokenTable(this.eos, this.name, name);
    }

    public makePlayerTable(name: string): PlayerTable {
        return new PlayerTable(this.eos, this.name, name);
    }

    public addGame(game: string): Promise<TransactionReceipt> {
        return this.pushAction('addgame', {game});
    }

    public updateToken(game: string, token: Token, contract: string,
                       min: number, max: number, balance: number): Promise<TransactionReceipt> {
        return this.pushAction('updatetoken', {game, token: token.symbol, contract, min, max, balance});
    }
}
