import {Table} from '@/modules/eos/table';
import {EOS} from '@/modules/eos/eos';
import {BaseBlackjackGame, BlackJackStatus} from '@/modules/contract/blackjack/blackjackGame';
import {isPresent} from '@/modules/utils/commonFunctions';


export class BlackjackGame extends BaseBlackjackGame {
    public readonly startTime: number;
    public readonly status: BlackJackStatus;

    public constructor(value: any) {
        super(value);

        if (isPresent(value)) {
            this.startTime = parseInt(value.start_time, 10);
            this.status = value.status;
        } else {
            this.startTime = 0;
            this.status = BlackJackStatus.Unknown;
        }

    }

    public isActive(): boolean {
        return this.status === BlackJackStatus.Active;
    }

    public canInsure(): boolean {
        return this.isActive() && this.bankerCards.length === 1 && this.bankerCards[0].isAce();
    }

    public canDouble(): boolean {
        return this.isActive() && this.playerCards.length === 2;
    }
}

export class BlackjackGameTable extends Table<BlackjackGame> {
    constructor(eos: EOS, contract: string) {
        super(eos, 'games', 'player', contract);
    }

    protected convert(row: any[]): BlackjackGame[] {
        return row.map((value) => new BlackjackGame(value)).sort((a, b) =>
                b.closeTime - a.closeTime,
        );
    }
}
