import {Contract} from '@/modules/eos/contract';
import {HouseContract} from '@/modules/contract/house/houseContract';
import {TokenTable} from '@/modules/contract/house/tokenTable';
import {GlobalTable} from '@/modules/contract/house/globalTable';
import {PlayerTable} from '@/modules/contract/house/playerTable';

export class GameContract extends Contract {
    public readonly tokens: TokenTable;
    public readonly globals: GlobalTable;
    public readonly players: PlayerTable;

    public constructor(name: string, protected readonly house: HouseContract) {
        super(name, house.eos);
        this.tokens = house.makeTokenTable(this.name);
        this.players = house.makePlayerTable(this.name);
        this.globals = new GlobalTable(house.eos, this.name);
    }
}
