import {Table} from '@/modules/eos/table';
import {EOS} from '@/modules/eos/eos';
import {isPresent} from '@/modules/utils/commonFunctions';
import {Card} from '@/modules/contract/cards';

export enum BaccaratBetType {
    Unknown = 0, BankerWin = 1, PlayerWin = 2, Tie = 4, BankerPair = 8, PlayerPair = 16,
}

export enum BaccaratStatus {
    Unknown, StandBy, Active,
}

export class BaccaratGame {
    public static betTypeToString(betType: BaccaratBetType): string {
        switch (betType) {
            case BaccaratBetType.BankerWin:
                return 'Banker Win';
            case BaccaratBetType.PlayerWin:
                return 'Player Win';
            case BaccaratBetType.Tie:
                return 'Tie';
            case BaccaratBetType.BankerPair:
                return 'Banker Pair';
            case BaccaratBetType.PlayerPair:
                return 'Player Pair';
            default:
                return '';
        }
    }

    public readonly id: number;
    public readonly endTime: number;
    public readonly status: BaccaratStatus;
    public readonly symbol: string;

    public readonly playerCards: Card[];
    public readonly bankerCards: Card[];
    public readonly playerPoints: number;
    public readonly bankerPoints: number;
    public readonly result: BaccaratBetType;
    public readonly isPlayerPair: boolean;
    public readonly isBankerPair: boolean;

    public constructor(value: any) {
        if (isPresent(value)) {
            this.id = value.id;
            this.endTime = value.end_time;
            this.status = value.status === 1 ? BaccaratStatus.StandBy : BaccaratStatus.Active;
            this.symbol = value.symbol;
        } else {
            this.id = 0;
            this.endTime = 0;
            this.status = BaccaratStatus.Unknown;
            this.symbol = '';
        }

        if (this.status === BaccaratStatus.StandBy) {
            this.playerCards = value.player_cards.map((card: number) => new Card(card));
            this.bankerCards = value.banker_cards.map((card: number) => new Card(card));

            this.playerPoints = this.calculatePoint(this.playerCards);
            this.bankerPoints = this.calculatePoint(this.bankerCards);
            this.isPlayerPair = this.isPair(this.playerCards);
            this.isBankerPair = this.isPair(this.bankerCards);

            if (this.playerPoints > this.bankerPoints) {
                this.result = BaccaratBetType.PlayerWin;
            } else if (this.bankerPoints > this.playerPoints) {
                this.result = BaccaratBetType.BankerWin;
            } else {
                this.result = BaccaratBetType.Tie;
            }
        } else {
            this.playerPoints = 0;
            this.bankerPoints = 0;
            this.playerCards = [];
            this.bankerCards = [];
            this.result = BaccaratBetType.Unknown;
            this.isPlayerPair = false;
            this.isBankerPair = false;
        }
    }

    public canStartBet() {
        const timestamp: number = Date.now() / 1000;
        return (this.status === BaccaratStatus.StandBy && timestamp >= this.endTime) ||
                (this.status === BaccaratStatus.Active && timestamp < this.endTime);
    }

    public isActive(): boolean {
        return this.status === BaccaratStatus.Active;
    }

    public calculatePoint(cards: Card[]): number {
        let point: number = 0;
        for (const card of cards) {
            if (card.value < 10) {
                point += card.value;
            }
        }
        return point % 10;
    }

    public isPair(cards: Card[]): boolean {
        return cards.length >= 2 && cards[0].value === cards[1].value;
    }
}

export class BaccaratGameTable extends Table<BaccaratGame> {
    constructor(eos: EOS, contract: string) {
        super(eos, 'games', 'symbol', contract);
    }

    protected convert(row: any[]): BaccaratGame[] {
        return row.map((value) => new BaccaratGame(value));
    }
}
