import {isEmptyString} from '@/modules/utils/commonFunctions';

export class Dates {
    public static initialize() {
        if (new Date('1970-01-01T00:00:00').getTime() === 0) {
            this.ISO_SUFFIX = '';
        } else if (new Date('1970-01-01T00:00:00-0000').getTime() === 0) {
            this.ISO_SUFFIX = '-0000';
        }
    }

    public static toISOTimestamp(str: string): number {
        return new Date(str + this.ISO_SUFFIX).getTime() / 1000;
    }

    private static ISO_SUFFIX: string = '';
}
Dates.initialize();

export class Timestamp {
    public readonly value: number;
    public constructor(private readonly str: string) {
        if (isEmptyString(str)) {
            this.value = 0;
        } else {
            this.value = Dates.toISOTimestamp(str);
        }
    }

    public toString() {
        return this.str;
    }
}
