import {DiceContract, RollMode} from './diceContract';
import {Asset} from '../../eos/asset';
import {Token} from '../../eos/token';
import {EOS} from '../../eos/eos';
import {EOSAccountsTable} from '../../eos/accountsTable';
import {TransactionReceipt} from '../../eos/transactionReceipt';
import {DiceHistoryRow} from './diceHistoryTable';
import {isPresent} from '../../utils/commonFunctions';
import {BetCallback, BetController} from '../betController';
import {HouseContract} from '@/modules/contract/house/houseContract';

export class DiceController implements BetCallback {
    public betNumber: number = 49;
    public allBetTicker: any = null;
    public betInfoTicker: any = null;
    public contractTicker: any = null;
    public globalTicker: any = null;
    public rollAnimationTicker: any = null;

    public allTableLoading: boolean = true;

    public rollValue: string = '00';
    public pendingRollValue: number = 0;
    public curBetId: number = 0;
    public activeIndex: number = 0;
    public rollMode: RollMode = RollMode.Under;
    public rollReal: string = '';
    public showedBetIds: any = {};

    public betAccountBalance: Asset = new Asset(10000, Token.EOS);
    public valueRoll: number = 50;
    public allData: DiceHistoryRow[] = [];
    public rollStartTime: number = 0;

    public readonly betControl: BetController;
    private readonly dice: DiceContract;

    public constructor(private readonly eos: EOS) {
        this.betControl = new BetController(this.eos, this);
        this.dice = new DiceContract(new HouseContract(this.eos));
    }

    public isLoggedIn(): boolean {
        return this.eos.loggedIn();
    }

    public isRollUnder(): boolean {
        return this.rollMode === RollMode.Under;
    }

    public curOdds(): string {
        return ((100 - 2) / this.curWinChance()).toFixed(2);
    }

    public curProfit() {
        return (this.betControl.amount * ((100 - 2) / this.curWinChance())).toFixed(2);
    }

    public curWinChance(): number {
        return this.getWinChance(this.rollMode, this.betNumber);
    }

    public initialize(): void {
        this.updateContractBalance();
        this.startTicker();
    }

    public destroy(): void {
        this.stopTicker();
    }

    public handleOver(): void {
        this.rollMode = RollMode.Over;
    }

    public handleUnder(): void {
        this.rollMode = RollMode.Under;
    }

    public getWinChance(rollType: RollMode, betNumber: number): number {
        return rollType === RollMode.Under ? betNumber : 99 - betNumber;
    }

    public getOdds(rollType: RollMode, betNumber: number) {
        return ((100 - 2) / this.getWinChance(rollType, betNumber));
    }

    public startTicker(): void {
        const that: DiceController = this;
        this.stopTicker();

        this.allBetTicker = setInterval(() => {
            that.updateAllBetTable();
        }, 5000);
    }

    public stopTicker(): void {
        if (this.allBetTicker !== null) {
            clearInterval(this.allBetTicker);
            this.allBetTicker = null;
        }

        if (this.contractTicker !== null) {
            clearInterval(this.contractTicker);
            this.contractTicker = null;
        }

        if (this.globalTicker !== null) {
            clearInterval(this.globalTicker);
            this.globalTicker = null;
        }
    }

    public async updateContractBalance(): Promise<any> {
        this.betAccountBalance = await new EOSAccountsTable(this.eos, this.dice).getBalance();
    }

    public getTimeStamp(): number {
        const date = new Date();
        return Math.floor(date.getTime() / 1000);
    }

    public async updateAllBetTable(): Promise<any> {
        this.allData = await this.dice.historyTable.readAll();
        this.allTableLoading = false;
    }

    public async getRollValueFromTrans(id: string, retry: number): Promise<any> {
        try {
            const trans: any = await this.eos.getTransaction(id);
            const targetValue = trans.block_num;
            if (retry === 1) {
                this.pendingRollValue = targetValue;
            } else if (this.pendingRollValue !== targetValue) {
                this.getRollValueFromTrans(id, 1);
                return;
            }

            const endTime = this.getTimeStamp();
            if (retry >= 3 || (endTime - this.rollStartTime) >= 5) {
                const result: DiceHistoryRow | undefined = this.getRollValueFromTable(this.rollStartTime);
                if (isPresent(result)) {
                    const rollValue = result!.rollNumber;
                    this.rollValue = rollValue < 10 ? '0' + rollValue : rollValue.toString(10);
                    this.betControl.finishBet(result!.profit.quantity);
                    return;
                }
            }
        } catch (e) {
            console.log(e);
        }

        setTimeout(() => {
            this.getRollValueFromTrans(id, retry + 1);
        }, 1000);
    }

    public getRollValueFromTable(time: number): DiceHistoryRow | undefined {
        const accountName: string = this.eos.account.name;
        for (const bet of this.allData) {
            if (bet.player === accountName && bet.timestamp.value >= time) {
                return bet;
            }
        }
        return undefined;
    }

    public betText(): string {
        return (this.isRollUnder() ? 'Roll Under ' : 'Roll Over ') + this.betNumber;
    }

    public async makeBet(amount: number): Promise<any> {
        const result: TransactionReceipt = await this.dice
                .play(this.rollMode, this.betNumber, new Asset(amount, Token.EOS));

        this.rollStartTime = this.getTimeStamp();
        this.getRollValueFromTrans(result.id, 1);
    }

    public canMakeBet(): boolean {
        return true;
    }
}
