import Vue from 'vue';
import VueI18n from 'vue-i18n';
import EN from './en';

Vue.use(VueI18n);

function language(): string {
    const sessionLang: string | null = sessionStorage.getItem('language');
    return sessionLang === null ? navigator.language : sessionLang;
}

const i18n: VueI18n = new VueI18n({
  locale: language(),
  messages: {
    // 'zh-CN': require('./zh'),
    'en-US': EN,
  },
});
export default i18n;
