import {Table} from '../../eos/table';
import {EOS} from '../../eos/eos';

export interface TokenRow {
    sym: string;
    contract: string;
    in: number;
    out: number;
    play_times: number;

    min: number;
    max: number;
    balance: number;
}

export class TokenTable extends Table<TokenRow> {
    constructor(eos: EOS, contract: string, scope: string = contract) {
        super(eos, 'tokens', 'sym', contract, scope);
    }

    protected convert(row: any[]): TokenRow[] {
        return row as TokenRow[];
    }
}
