import {Card} from '@/modules/contract/cards';
import {Asset} from '@/modules/eos/asset';
import {isPresent} from '@/modules/utils/commonFunctions';
import {Token} from '@/modules/eos/token';

export enum BlackJackStatus {
    Unknown, Active, Stood, Close,
}

export enum BlackjackResult {
    Unknown, Surrender, Win, Lose, Push,
}

export class BaseBlackjackGame {
    public static MAX_POINTS: number = 21;
    public static calculatePoints(cards: Card[]) {
        let points: number = 0;
        let countA: number = 0;

        for (const card of cards) {
            const point: number = Math.min(card.value, 10);
            if (card.value === 1) {
                countA ++;
            }
            points += point;
        }

        for (let i = 0; i < countA; i++) {
            if (points + 10 > 21) {
                return points;
            } else {
                points += 10;
            }
        }
        return points;
    }

    public readonly id: number;
    public readonly closeTime: number;
    public readonly playerCards: Card[];
    public readonly bankerCards: Card[];
    public readonly player: string;
    public readonly bet: Asset;
    public readonly insured: boolean;
    public readonly status: BlackJackStatus;
    public readonly result: BlackjackResult;

    public readonly bankerPoints: number;
    public readonly playerPoints: number;

    public constructor(value: any) {
        if (isPresent(value)) {
            this.id = value.id;
            this.closeTime = parseInt(value.close_time, 10);

            this.playerCards = value.player_cards.map((card: number) => new Card(card));
            this.bankerCards = value.banker_cards.map((card: number) => new Card(card));

            this.player = value.player;
            this.bet = Asset.fromString(value.bet);
            this.insured = value.insured;
            this.result = value.result;
            this.status = value.status;

            this.bankerPoints = BaseBlackjackGame.calculatePoints(this.bankerCards);
            this.playerPoints = BaseBlackjackGame.calculatePoints(this.playerCards);
        } else {
            this.id = 0;
            this.closeTime = 0;
            this.player = '';

            this.playerCards = [];
            this.bankerCards = [];

            this.bet = new Asset(0, Token.EOS);
            this.insured = false;
            this.result = BlackjackResult.Unknown;
            this.status = BlackJackStatus.Unknown;

            this.bankerPoints = 0;
            this.playerPoints = 0;
        }
    }

    public isBankerBusted(): boolean {
        return this.bankerPoints > BaseBlackjackGame.MAX_POINTS;
    }

    public isPlayerBusted(): boolean {
        return this.playerPoints > BaseBlackjackGame.MAX_POINTS;
    }

    public isBankerBlackjack(): boolean {
        return this.bankerPoints === BaseBlackjackGame.MAX_POINTS && this.bankerCards.length === 2;
    }

    public isPlayerBlackjack(): boolean {
        return this.playerPoints === BaseBlackjackGame.MAX_POINTS && this.playerCards.length === 2;
    }

    public isWin(): boolean {
        return this.result === BlackjackResult.Win;
    }

    public isLose(): boolean {
        return this.result === BlackjackResult.Lose;
    }

    public isPush(): boolean {
        return this.result === BlackjackResult.Push;
    }
}
