export class Promises {
    public static make<T>(result: T): Promise<T> {
        return new Promise<T>((resolve) => resolve(result));
    }

    public static delay(milliseconds: number): Promise<any> {
        return new Promise((resolve) => {
            setTimeout(() => resolve(), milliseconds);
        });
    }

    public static delayUntil<T>(promise: Promise<T>, milliseconds: number): Promise<T> {
        return Promise.all([promise, this.delay(milliseconds)])
            .then((r) => r[0]);
    }
}
