import {Token} from '../../eos/token';
import {EOS} from '../../eos/eos';
import {isPresent} from '../../utils/commonFunctions';
import {BetCallback, BetController} from '../betController';
import {BaccaratBetType, BaccaratGame, BaccaratStatus} from './baccaratGameTable';
import {BaccaratContract} from './baccaratContract';
import {BaccaratBet} from '@/modules/contract/baccarat/baccaratBetTable';
import {TransactionReceipt} from '@/modules/eos/transactionReceipt';

export class BaccaratController implements BetCallback {
    public gameTicker: any = undefined;
    public betTicker: any = undefined;

    public gameGlobals: any = undefined;
    public game: BaccaratGame = new BaccaratGame(undefined);
    public bets: BaccaratBet[] = [];
    public betType: BaccaratBetType = BaccaratBetType.Unknown;
    public message: string = '';

    public readonly betControl: BetController;
    private readonly baccarat: BaccaratContract;

    public constructor(public readonly eos: EOS) {
        this.betControl = new BetController(this.eos, this);
        this.baccarat = new BaccaratContract(this.eos);
    }

    public async initialize(): Promise<any> {
        this.startTick();
    }

    public destroy(): void {
        this.stopTick();
    }

    public async loadGame(): Promise<any> {
        if (!this.eos.loggedIn()) {
            throw new Error('Not logged in');
        }
        const game: BaccaratGame | undefined = await this.baccarat.gameTable.getById(Token.EOS.toString());

        if (isPresent(game)) {
            this.game = game!;
        }
    }

    public clearMyGameInfo(): void {
        this.game = new BaccaratGame(undefined);
    }

    public setBetType(betType: BaccaratBetType): void {
        this.betType = betType;
    }

    public async startGetGame(delay: number = 2000, txId: string = '', callback?: () => any): Promise<any> {
        this.stopGameTicker();
        this.gameTicker = setTimeout(async () => {
            try {
                await this.loadGame();
                this.stopGameTicker();

                if (isPresent(callback)) {
                    callback!();
                }
                this.betControl.finishBet(-1);
            } catch (error) {
                this.startGetGame(500, '', callback);
            }
        }, delay);
    }

    public startTick(): void {
        this.gameTicker = setInterval(() => {
            this.update();
        }, 1000);
    }

    public update(): void {
        const timestamp: number = Math.ceil(Date.now() / 1000);
        switch (this.game.status) {
            case BaccaratStatus.Active:
                this.message = Math.max(0, this.game.endTime - timestamp) + ' seconds left';
                break;
            case BaccaratStatus.StandBy:
                const resultString: string = 'Round finished, ' + BaccaratGame.betTypeToString(this.game.result);
                if (timestamp > this.game.endTime) {
                    this.message = resultString + '. Place bet to start next round.';
                } else {
                    this.message = resultString + '. ' + (this.game.endTime - timestamp) + ' seconds until next round';
                }
                break;
            default:
                this.message = 'Loading';
        }

        if (timestamp % 2 === 0) {
            if (timestamp > this.game.endTime ) {
                this.loadGame();
            }

            if (this.game.isActive()) {
                this.updateBets();
            }
        }
    }

    public stopTick() {
        if (isPresent(this.gameTicker)) {
            clearInterval(this.gameTicker);
        }
        this.gameTicker = undefined;
    }

    public stopGameTicker() {
        if (this.gameTicker !== null) {
            clearTimeout(this.gameTicker);
            this.gameTicker = null;
        }
    }

    public async updateBets() {
        this.bets = await this.baccarat.betTable.readAll();
    }

    public starBetTick() {
        this.stopBetTick();
        this.updateBets();

        this.betTicker = setTimeout(() => {
            this.starBetTick();
        }, 2000);
    }

    public stopBetTick() {
        if (this.betTicker !== null) {
            clearTimeout(this.betTicker);
            this.betTicker = null;
        }
    }

    public betText(): string {
        return 'Play';
    }

    public canMakeBet(): boolean {
        return this.game.canStartBet() && this.betType !== BaccaratBetType.Unknown;
    }

    public async makeBet(amount: number): Promise<TransactionReceipt> {
        if (this.betType === BaccaratBetType.Unknown) {
            throw new Error('Bet type not selected');
        }
        this.betControl.finishBet(0);
        return this.baccarat.play(this.game.id, this.betType, this.betControl.getBetAsset());
    }
}
