import {Table} from '@/modules/eos/table';
import {EOS} from '@/modules/eos/eos';
import {Asset} from '@/modules/eos/asset';
import {Token} from '@/modules/eos/token';

export class PlayerRow {
    public readonly name: string;
    public readonly in: Asset;
    public readonly out: Asset;
    public readonly playTimes: number;
    public readonly lastPlayTime: number;
    public readonly eventIn: Asset;

    public constructor(value: any) {
        this.name = value.name;
        this.in = new Asset(value.in, Token.EOS);
        this.out = new Asset(value.out, Token.EOS);
        this.playTimes = value.play_times;
        this.lastPlayTime = value.last_play_time;
        this.eventIn = new Asset(value.event_in, Token.EOS);
    }
}

export class PlayerTable extends Table<PlayerRow> {
    constructor(eos: EOS, contract: string, scope: string) {
        super(eos, 'players', 'name', contract, scope);
    }

    protected convert(row: any[]): PlayerRow[] {
        return row.map((value) => new PlayerRow(value))
                .sort((a, b) => a.eventIn.quantity - b.eventIn.quantity);
    }
}
