import {Table} from '@/modules/eos/table';
import {EOS} from '@/modules/eos/eos';
import {Asset} from '@/modules/eos/asset';
import {Token} from '@/modules/eos/token';
import {RollMode} from '@/modules/contract/dice/diceContract';
import {Timestamp} from '@/modules/utils/dates';

export class DiceHistoryRow {
    public id: number;
    public betId: number;
    public player: string;
    public betAmount: Asset;

    public betNumber: number;
    public rollNumber: number;
    public payout: Asset;
    public profit: Asset;
    public rollMode: RollMode;

    public timestamp: Timestamp;

    constructor(value: any) {
        const token: Token = Token.fromSymbol(value.sym);
        this.id = value.id;
        this.betId = value.bet_id;
        this.player = value.player;

        this.betNumber = value.bet_value;
        this.rollNumber = value.roll_value;
        this.rollMode = value.roll_type;
        this.betAmount = Asset.fromRawAmount(value.bet, token);
        this.payout = Asset.fromRawAmount(value.payout, token);
        this.timestamp = new Timestamp(value.time);

        this.profit = this.isWin ? this.payout.minus(this.betAmount.quantity) : new Asset(0, Token.EOS);
    }

    public get isRollOver(): boolean {
        return this.rollMode === RollMode.Over;
    }

    public get isWin(): boolean {
        return this.payout.quantity !== 0;
    }

}

export class DiceHistoryTable extends Table<DiceHistoryRow> {
    constructor(eos: EOS, contract: string) {
        super(eos, 'history', 'id', contract);
    }

    protected convert(row: any[]): DiceHistoryRow[] {
        return row.map((value) => new DiceHistoryRow(value)).sort((a, b) =>
                b.timestamp.value - a.timestamp.value,
        );
    }
}
