import Vue from 'vue';
import Router from 'vue-router';
import DiceView from '@/views/dice/dice.vue';
import BaccaratView from '@/views/baccarat/baccarat.vue';
import BlackjackView from '@/views/blackjack/blackjack.vue';
import FunctionTests from '@/views/functionTests.vue';

Vue.use(Router);

export default new Router({
    linkActiveClass: 'active',
    linkExactActiveClass: 'active',
    routes: [
        {
            path: '/',
            name: 'Dice',
            component: DiceView,
        },
        {
            path: '/blackjack',
            name: 'Blackjack',
            component: BlackjackView,
        },
        {
            path: '/baccarat',
            name: 'Baccarat',
            component: BaccaratView,
        },
        {
            path: '/test',
            name: 'Test',
            component: FunctionTests,
        },
    ],
});
