












































































// @ts-ignore
import vueSlider from 'vue-slider-component';
import {Component, Vue} from 'vue-property-decorator';
import HistoryTableView from '@/views/dice/diceHistoryTableView.vue';
import Loading from '@/components/loading.vue';
import BetView from '@/components/betView.vue';
import {DiceController} from '../../modules/contract/dice/diceController';
import {EOS} from '../../modules/eos/eos';

@Component({
    components: {
        HistoryTableView,
        vueSlider,
        Loading,
        BetView,
    },
})
export default class DiceView extends Vue {
    public readonly control: DiceController = new DiceController(EOS.instance);
    public underSlide: any = {
        roll: this.control.betNumber,
        height: 12,
        dotWidth: 20,
        dotHeight: 24,
        min: 2,
        max: 97,
        speed: 0.1,
        tooltip: 'hover',
        bgStyle: {
            backgroundColor: 'transparent',
            border: '1px solid #938282',
        },
        tooltipStyle: {
            backgroundColor: '#666',
            borderColor: '#666',
        },
        processStyle: {
            backgroundColor: '#0DD985',
            margin: '-1px',
            height: '12px',
        },
    };

    public overSlide: any = {
        roll: this.control.betNumber,
        height: 12,
        dotWidth: 20,
        dotHeight: 24,
        min: 2,
        max: 97,
        speed: 0.1,
        tooltip: 'hover',
        bgStyle: {
            backgroundColor: '#0DD985',
            marginTop: '0px',
            height: '12px',
        },
        tooltipStyle: {
            backgroundColor: '#666',
            borderColor: '#666',
        },
        processStyle: {
            backgroundColor: '#230304',
            border: '1px solid #938282',
            boxShadow: '0 0 20px #230304',
        },
    };

    public mounted(): void {
        this.control.initialize();
    }

    public beforeDestroy(): void {
        this.control.destroy();
    }

    public get rollStyle(): any {
        if (this.control.isRollUnder) {
            return parseInt(this.control.rollValue, 10) < this.control.betNumber
                    ? {color: '#0dd985'} : {color: '#9f0000'};
        } else {
            return parseInt(this.control.rollValue, 10) > this.control.betNumber
                    ? {color: '#0dd985'} : {color: '#9f0000'};
        }
    }

    public get top(): string {
        return -this.control.activeIndex * 45 + 'px';
    }

    public get transition(): string {
        return this.control.activeIndex === 0 ? 'none' : 'top 0.5s';
    }
}


