




































import {Component, Vue} from 'vue-property-decorator';
import CardListView from '@/components/cardListView.vue';
import BetView from '@/components/betView.vue';
import BaccaratBetView from '@/views/baccarat/baccaratBetView.vue';
import {EOS} from '../../modules/eos/eos';
import {BaccaratController} from '../../modules/contract/baccarat/baccaratController';
import {BaccaratBetType} from '../../modules/contract/baccarat/baccaratGameTable';

class BetTypeOption {
    public constructor(public readonly betType: number,
                       public readonly title: string) {
    }
}

@Component({
    components: {
        BetView,
        BaccaratBetView,
        CardListView,
    },
})
export default class BaccaratView extends Vue {
    public readonly control: BaccaratController = new BaccaratController(EOS.instance);
    public betButtons: any[] = [
        new BetTypeOption(BaccaratBetType.BankerWin, 'Banker Win'),
        new BetTypeOption(BaccaratBetType.PlayerWin, 'Player Win'),
        new BetTypeOption(BaccaratBetType.Tie, 'Tie'),
        new BetTypeOption(BaccaratBetType.BankerPair, 'Banker Pair'),
        new BetTypeOption(BaccaratBetType.PlayerPair, 'Player Pair'),
    ];

    public mounted(): void {
        this.control.initialize();
    }

    public beforeDestroy(): void {
        this.control.destroy();
    }
}

