import {Contract} from '../../eos/contract';
import {EOS} from '../../eos/eos';
import {GlobalTable} from '@/modules/contract/house/globalTable';
import {TokenTable} from '@/modules/contract/house/tokenTable';
import {Asset} from '@/modules/eos/asset';
import {DiceHistoryTable} from '@/modules/contract/dice/diceHistoryTable';
import {TransactionReceipt} from '@/modules/eos/transactionReceipt';
import {Constants} from '@/modules/eos/constants';
import {Network} from '@/modules/eos/network';
import {HouseContract} from '@/modules/contract/house/houseContract';
import {GameContract} from '@/modules/contract/house/gameContract';

export enum RollMode {
    Under = 1,
    Over = 2,
}

export class DiceContract extends GameContract {
    public static readonly CONTRACT = Constants.NETWORK === Network.LOCALHOST ? 'dice' : 'godappdice12';

    public readonly historyTable: DiceHistoryTable;

    public constructor(house: HouseContract) {
        super(DiceContract.CONTRACT, house);
        this.historyTable = new DiceHistoryTable(house.eos, this.name);
    }

    public play(rollMode: RollMode, betNumber: number, asset: Asset): Promise<TransactionReceipt> {
        return this.house.transfer(
            asset, `${this.name},${rollMode},${betNumber},${''},`,
        );
    }
}
